.menu {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 63.5px;
    left: 0px;
    transform: translateX(-120%);
    transition: all 0.5s;
}

.menu.active {
    transform: translateX(0);
}

.blur {
    width: 100vw;
    height: 100vh;
    left: 20%;
    backdrop-filter: blur(2px);
    position: absolute;
}

.menuContent {
    width: 20%;
    height: 100%;
    background-color: aqua;
    display: flex;
    flex-direction: column;
    border-right: 1px solid black;
    transition: all 0.6s;
}

.wrap {
    margin-top: 40px;
}

.menuHeader>a,
a:visited {
    color: white;
    text-shadow: 1px 2px 3px black;
    width: 100%;
    padding: 2px 10px;
    position: relative;
    bottom: 3px;
}

.menuHeader {
    padding: 5px;
    font-size: 2rem;
    color: white;
    text-shadow: 1px 2px 3px black;
    margin: 3px 10px;
    z-index: 10;
}

.menuHeader>.activeLink {
    text-shadow: 1px 2px 3px rgba(229, 255, 0, 0.61);
    font-weight: bold;
    transition-duration: 0.2s;
}

@media screen and (max-width:1181px) {
    .menuContent {
        width: 30%;
    }
}

@media screen and (max-width:768px) {
    .menuContent {
        width: 100%;
    }
    .blur {
        backdrop-filter: none;
    }
}